<template>
  <v-app class="white">

    <app-toolbar
      is-dark="false"
      img-logo="img/alpha-creative-logo.png"
      :menu-items="menuItems"
    ></app-toolbar>

    <app-jumbotron
      hero-image="img/hero.jpg"
      gradient="to top, rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.75)"
      title="Hi! I'm Michael George"
      sub-title="I am a software engineer based in Toronto">
    </app-jumbotron>

    <v-content style="padding: 64px 0px 0px;">
      <router-view/>
    </v-content>

    <app-footer
      :social-links="socialLinks">
    </app-footer>

  </v-app>
</template>

<script>
import Toolbar from '@/components/Toolbar.vue'
import Jumbotron from '@/components/Jumbotron.vue'
import Footer from '@/components/Footer.vue'

export default {
  data: () => ({
    menuItems: [
      { title: 'About me', link: { hash: '#about-me' }, name: 'about-me' },
      { title: 'Portfolio', link: { hash: '#portfolio' }, name: 'portfolio' },
      { title: 'Contact me', link: { hash: '#contact-me' }, name: 'contact-me' }
    ],
    socialLinks: [
      { icon: 'mdi-github-circle', name: 'Github' },
      { icon: 'mdi-twitter-circle', name: 'Twitter' },
      { icon: 'mdi-facebook-box', name: 'Facebook' },
      { icon: 'mdi-instagram', name: 'Instagram' }
    ]
  }),
  components: {
    AppToolbar: Toolbar,
    AppJumbotron: Jumbotron,
    AppFooter: Footer }
}
</script>



