<template>
  <v-jumbotron
    height="40vh"
    :src="heroImage"
    :gradient="gradient">
    <v-container fill-height style="transform-origin: center top 0px;">
      <v-layout align-center>
        <v-flex text-xs-center>
          <h1 class="display-2">{{ title }}</h1>
          <div class="subheading">{{ subTitle }}</div>
        </v-flex>
      </v-layout>
    </v-container>
  </v-jumbotron>
</template>

<script>
export default {
  name: 'Jumbotron',
  props: {
    heroImage: String,
    gradient: String,
    title: String,
    subTitle: String
  }
}
</script>



