<template>
  <div class="home">
    <v-container class="py-5">

      <v-layout wrap class="mb-5">
        <v-flex xs8>
          <h2 id="about-me" class="title mb-3">About Me</h2>
          <p>I am a full-stack engineer specializing in user interface development with over 6 years of experience creating responsive web sites and applications. I possess strong leadership qualities, communication skills, and excel within a team environment.</p>
        </v-flex>
        <v-flex xs4>
          <img src="img/about-me.png" alt="About Me" width="100%">
        </v-flex>
      </v-layout>

      <v-layout class="mb-5">
        <v-flex xs12>
          <h2 id="portfolio" class="title mb-3">Portfolio</h2>
          <alpha-gallery></alpha-gallery>
        </v-flex>
      </v-layout>

      <v-layout class="mb-5">
        <v-flex xs12>
          <h2 id="contact-me" class="title my-5 text-xs-center">Contact Me</h2>
          <v-layout justify-center>

            <a href="#" style="text-decoration: none;">
              <div class="avatar ma-3 blue" style="height: 76px; width: 76px;">
                <i aria-hidden="true" class="icon theme--dark mdi mdi-email" style="font-size: 36px;"></i>
              </div>
            </a>
            <a href="#" style="text-decoration: none;">
              <div class="avatar ma-3 red lighten-2" style="height: 76px; width: 76px;">
                <i aria-hidden="true" class="icon theme--dark mdi mdi-phone" style="font-size: 36px;"></i>
              </div>
            </a>
            <a href="#" style="text-decoration: none;">
              <div class="avatar ma-3 indigo" style="height: 76px; width: 76px;">
                <i aria-hidden="true" class="icon theme--dark mdi mdi-facebook" style="font-size: 36px;"></i>
              </div>
            </a>

          </v-layout>
        </v-flex>
      </v-layout>

    </v-container>
  </div>
</template>

<script>
import Gallery from '@/components/Gallery.vue'

export default {
  name: 'home',
  components: { AlphaGallery: Gallery }
}
</script>



