<template>
  <v-toolbar
    v-scroll="onScroll"
    fixed
    :dark="isDark == 'true'"
    :flat="!isScrolling"
    :color="!isScrolling ? 'transparent' : 'secondary'"
    height="64">
    <!-- <router-link :to="{ hash: '#app' }"><img :src="imgLogo"></router-link> -->
    <a href="/#app" v-smooth-scroll="{ duration: 500 }"><img :src="imgLogo"></a>
    <v-spacer></v-spacer>
    <v-toolbar-side-icon
      @click.stop="drawer = !drawer"
      class="hidden-md-and-up"
    ></v-toolbar-side-icon>
    <!-- ITEMS -->
    <v-btn
      flat
      v-smooth-scroll="{ duration: 400, offset: -100 }"
      v-for="(item, index) in menuItems" :key="index"
      :to="item.link"
      >{{ item.title }}
    </v-btn>
  </v-toolbar>
</template>

<script>
export default {
  name: 'AppToolbar',
  props: {
    isDark: String,
    imgLogo: String,
    menuItems: Array
  },
  data () {
    return {
      drawer: false,
      offsetTop: 0
    }
  },
  computed: {
    isScrolling () {
      if (this.offsetTop >= 100) {
        return true
      }
      return false
    }
  },
  methods: {
    onScroll (e) {
      this.offsetTop = window.pageYOffset || document.documentElement.scrollTop
    }
  }
}
</script>



