<template>
  <v-footer>
    <v-container class="alpha-footer" fluid>
      <v-layout blue-grey darken-2 fill-height row wrap>

        <v-flex xs12 sm3 class="blue-grey darken-3 pa-5">
          <h5 class="title pb-3">Alpha Software</h5>
          <ul>
            <!-- SOCIAL LINKS -->
            <li v-for="(link, index) in socialLinks" :key="index">
              <a href="#!" class="layout align-center">
                <i aria-hidden="true" class="icon mr-3 theme--dark mdi" :class="link.icon"></i>
                <span class="subheading">{{ link.name }}</span>
              </a>
            </li>
          </ul>
        </v-flex>

        <v-flex xs12 sm3 class="pa-5">
          <h5 class="title pb-3">Category 1</h5>
          <ul>
            <li v-for="(i, index) in 4" :key="index">
              <a href="javascript:;">Link {{ i }}</a>
            </li>
          </ul>
        </v-flex>
        <v-flex xs12 sm3 class="pa-5">
          <h5 class="title pb-3">Category 2</h5>
          <ul>
            <li v-for="(i, index) in 4" :key="index">
              <a href="javascript:;">Link {{ i }}</a>
            </li>
          </ul>
        </v-flex>
        <v-flex xs12 sm3 class="pa-5">
          <h5 class="title pb-3">Category 3</h5>
          <ul>
            <li v-for="(i, index) in 4" :key="index">
              <a href="javascript:;">Link {{ i }}</a>
            </li>
          </ul>
        </v-flex>

        <v-flex xs12>
          <v-card color="blue-grey darken-4" flat dark tile class="text-xs-center">
            <v-card-text>
              <span>
                © 2018 Alpha Software — All Rights reserved.
              </span>
            </v-card-text>
          </v-card>
        </v-flex>

      </v-layout>
    </v-container>
  </v-footer>
</template>

<script>
export default {
  name: 'AppFooter',
  props: {
    socialLinks: Array
  }
}
</script>

<style lang="stylus" scoped>
.alpha-footer
  background hsl(200, 16%, 22%)
  color hsl(0, 0%, 100%)
  display block
  padding 0!important
  height auto
  ul
    list-style none
    li
      margin-bottom 10px
    a
      color hsl(0, 0%, 100%)
      text-decoration none
      transition .2s ease-in
      &:hover
        opacity .5

@media all and (max-width: 520px)
  .display-2
    margin-top 35px
    font-size 25px !important
    line-height: 25px !important;
</style>



