import Vue from 'vue'
import App from './App.vue'
import router from './router'
import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  transitions,
  VJumbotron,
  VTabs,
  VCard
} from 'vuetify'
import { Scroll } from 'vuetify/es5/directives'
import vueSmoothScroll from 'vue2-smooth-scroll'
import '../node_modules/vuetify/src/stylus/main.styl'
import 'mdi/css/materialdesignicons.css'

Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    transitions,
    VJumbotron,
    VTabs,
    VCard
  },
  directives: {
    Scroll
  },
  theme: {
    primary: '#000000',
    secondary: '#FFFFFF',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})
Vue.use(vueSmoothScroll)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



