<template>
  <div class="alpha-gallery">
    <!-- TABS -->
    <v-tabs class="mb-5">
      <v-tab @click="searchTag = 'all'">All</v-tab>
      <v-tab @click="searchTag = 'tag01'">Skyscrapers</v-tab>
      <v-tab @click="searchTag = 'tag02'">Government</v-tab>
      <v-tab @click="searchTag = 'tag03'">Customized</v-tab>
    </v-tabs>

    <v-container grid-list-xl class="pa-0">
      <transition-group name="fade-transition" tag="div" class="layout wrap child-flex" style="min-height: 450px;">

        <!-- IMAGES -->
        <v-flex xs4 v-for="element in filteredGallery" :key="element.id" class="gallery-card">
          <div
            class="white--text card card--hover"
            :style="{ 'height': '200px', 'background': 'url(' + element.src + ') center center / cover no-repeat' }">
          </div>
        </v-flex>

      </transition-group>
    </v-container>

  </div>
</template>

<script>
export default {
  name: 'gallery',
  data () {
    return {
      searchTag: 'all',
      images: [
        { id: 1, src: 'img/project1.jpg', tags: ['all', 'tag01', 'tag03'] },
        { id: 2, src: 'img/project2.jpg', tags: ['all', 'tag02', 'tag03'] },
        { id: 3, src: 'img/project3.jpg', tags: ['all', 'tag03'] },
        { id: 4, src: 'img/project4.jpg', tags: ['all', 'tag01', 'tag02'] },
        { id: 5, src: 'img/project5.jpg', tags: ['all', 'tag02'] },
        { id: 6, src: 'img/project6.jpg', tags: ['all', 'tag01'] }
      ]
    }
  },
  computed: {
    filteredGallery () {
      return this.images.filter((item) => {
        return (item.tags.indexOf(this.searchTag) >= 0)
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.gallery-card
  transform-origin center center 0
  transition .3s cubic-bezier(.25,.8,.5,1)

.fade-transition-enter-active,
.fade-transition-leave-active
  transition .3s cubic-bezier(.25, .8, .5, 1)

.fade-transition-enter,
.fade-transition-leave-to
  opacity 0

.fade-transition-leave-active,
.fade-transition-leave-to,
.fade-transition-leave
  display none
</style>



